.intro {
  display: flex;
  width: 100%;
  background-color: $color-white-smoke;

  &__container {
    flex-grow: 1;
    display: flex;
    flex-wrap: nowrap;
    width: 100%;

    @include vp-1499 {
      flex-wrap: wrap;
    }
  }

  .swiper-next {
    position: absolute;
    z-index: 100;
    bottom: 24%;
    right: 18.7%;
    cursor: pointer;

    @include vp-767 {
      bottom: 27.3%;
      right: 4%;
    }

    svg {
      display: block;
      width: 104px;
      height: 21px;

      @include vp-767 {
        width: 64px;
        height: 14px;
      }
    }
  }

  .swiper-pagination {
    width: max-content;
    bottom: 24%;
    right: 29.15%;
    left: auto;

    @include vp-1499 {
      right: 34%;
    }

    @include vp-767 {
      bottom: 27.2%;
      right: 34%;
    }
  }

  .swiper-pagination-bullet {
    @include vp-767 {
      width: 3px;
      height: 3px;
    }
  }

  .swiper-pagination-bullet-active {
    background: $color-bunker;
    transform: scale(1.85);

    @include vp-767 {
      transform: scale(1.7);
    }
  }

  &__left {
    flex-grow: 1;
    display: flex;
    position: relative;
    min-width: 509px;
    background-color: $color-snow;
    overflow: hidden;

    .title {
      display: block;
      max-width: 185px;
      margin: auto 10px auto 40px;
      padding-top: 12px;
    }

    @include vp-1499 {
      width: 100%;
    }
  }

  &__img {
    position: absolute;
    top: -247px;
    left: 0;
    width: 716px;
    min-height: 500px;
    opacity: 0.15;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      transform: scale(1.52);
    }
  }
}