@media (max-width: 767px) {
  .footer__nav:last-child {
    grid-column: 1/2;
    order: 1;
  }
}
@media (max-width: 767px) {
  .footer__nav:nth-child(2) {
    grid-column: 2/3;
    order: 2;
  }
}
@media (max-width: 767px) {
  .footer__nav:first-child {
    grid-column: 1/-1;
    order: 3;
  }
}
@media (max-width: 767px) {
  .footer__nav:first-child .footer__list {
    column-count: 2;
  }
}
.footer__list {
  width: 220px;
  color: #292d32;
}
@media (max-width: 767px) {
  .footer__list {
    width: auto;
  }
}
.footer__list li {
  margin-bottom: 5px;
}
.footer__list li:last-child {
  margin-bottom: 0;
}
.footer__contacts {
  width: 100%;
  padding-top: 38px;
  border-top: 1px solid #e5e5e5;
}
@media (max-width: 767px) {
  .footer__contacts {
    margin-top: -2px;
    padding-top: 32px;
  }
}
.footer__contacts-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .footer__contacts-list {
    flex-direction: column;
    gap: 21px;
  }
}
.footer__contacts-item {
  display: flex;
  flex-direction: column;
  gap: 13px;
}
@media (max-width: 767px) {
  .footer__contacts-item {
    gap: 0;
  }
}
.footer__social {
  display: flex;
  gap: 20px;
  margin-top: 10px;
}
@media (max-width: 767px) {
  .footer__social {
    gap: 30px;
  }
}
.footer__social-link {
  display: block;
  width: 38px;
  height: 38px;
}
@media (max-width: 767px) {
  .footer__social-link {
    width: 30px;
    height: 30px;
  }
}
.footer__social-link svg {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.footer.bg-text::after {
  z-index: -1;
  right: -10px;
  top: 128px;
  width: 25vw;
  height: 9.2vw;
  background-image: url("../img/dell-footer.svg");
}
@media (max-width: 767px) {
  .footer.bg-text::after {
    right: -34px;
    top: auto;
    bottom: 38px;
    width: 70%;
    height: 14.6vw;
    background-image: url("../img/dell.svg");
  }
}

@media (max-width: 767px) {
  .no-mob {
    display: none;
  }
}

.no-desc {
  display: none;
}
@media (max-width: 767px) {
  .no-desc {
    display: block;
  }
}