@mixin hover {
  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover {
      @content;
    }
  }
}


@mixin vp-1919 {
  @media (max-width: ($vp-1920 - 1)) {
    @content;
  }
}

@mixin vp-1499 {
  @media (max-width: ($vp-1500 - 1)) {
    @content;
  }
}

@mixin vp-1299 {
  @media (max-width: ($vp-1300 - 1)) {
    @content;
  }
}

@mixin vp-1023 {
  @media (max-width: ($vp-1024 - 1)) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: ($vp-768 - 1)) {
    @content;
  }
}
